import React, {useState, useRef, useEffect} from 'react';
import {defaultProps, propTypes} from '../components/Display/DvpTable.react';
import RawHTML from '../components/Utils/RawHTML.react';
import useCss from '../hooks/useCss';
import {isNumber, isEqual, isString, isUndefined, isArray} from 'lodash';
import {Space, Table, Button, Input, Checkbox, Tag, Radio, Tooltip} from 'antd';
import {
    SearchOutlined,
    QuestionCircleOutlined,
    DownOutlined,
    CheckCircleFilled,
} from '@ant-design/icons';
import DvpIcon from '../components/General/DvpIcon.react';

/**
 * An Ant Design Table component
 * See https://ant.design/components/table
 */
const DvpTable = (props) => {
    let {
        id,
        className,
        style,
        pagination,
        columns,
        selectedColumns,
        columnAttrs,
        data,
        bordered,
        maxHeight,
        maxWidth,
        size,
        cellUpdateOptimize,
        selectedValues,
        ellipsisMaxLen,
        setProps,
        loading_state,
    } = props;
    // default selected values
    let checkedValues = selectedValues;
    if (!className) {
        className = '';
    }
    className = [className, 'dvp-table'].join(' ');

    // sort columns
    let cols = columns;
    if (!columns && selectedColumns.length > 0) {
        cols = selectedColumns.map((d, i) => {
            return {
                dataIndex: d,
                key: d,
                title: d,
            };
        });
        data = data.map((record, index) => {
            return _.pick(record, selectedColumns);
        });
    }

    // set key for data
    for (let i = 0; i < data.length; i++) {
        if (!data[i].hasOwnProperty('key')) {
            data[i]['key'] = Number(i);
        }
    }

    const [searchText, setSearchText] = useState('');
    const [searchedColumn, setSearchedColumn] = useState('');
    const [displayData, setDisplayData] = useState(
        pagination ? data.slice(0, pagination.defaultPageSize) : data
    );

    // some functions
    const filterObjNa = (obj) => {
        let new_dict = {};
        Object.entries(obj).map(([k, v]) => {
            if (!isUndefined(v) && v !== null) {
                new_dict[k] = v;
            }
        });
        return new_dict;
    };
    const handleSearch = (selectedKeys, confirm, dataIndex) => {
        confirm();
        setSearchText(selectedKeys[0]);
        setSearchedColumn(dataIndex);
    };

    const handleReset = (clearFilters, confirm, dataIndex) => {
        clearFilters();
        setSearchText('');
        confirm();
        setSearchedColumn(dataIndex);
    };

    const getColumnSearchProps = (dataIndex) => ({
        filterDropdown: ({
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
        }) => (
            <div style={{padding: 8}} onKeyDown={(e) => e.stopPropagation()}>
                <Input
                    // ref={node => {searchInput = node}}
                    value={selectedKeys[0]}
                    onChange={(e) =>
                        setSelectedKeys(e.target.value ? [e.target.value] : [])
                    }
                    onPressEnter={() =>
                        handleSearch(selectedKeys, confirm, dataIndex)
                    }
                    style={{marginBottom: 8, display: 'block'}}
                />
                <Space>
                    <Button
                        type="primary"
                        onClick={() =>
                            handleSearch(selectedKeys, confirm, dataIndex)
                        }
                        icon={<SearchOutlined />}
                        size="small"
                        style={{width: 90}}
                    >
                        Search
                    </Button>
                    <Button
                        onClick={() =>
                            clearFilters &&
                            handleReset(clearFilters, confirm, dataIndex)
                        }
                        size="small"
                        style={{width: 90}}
                    >
                        Reset
                    </Button>
                </Space>
            </div>
        ),
        filterIcon: (filtered) => (
            <SearchOutlined
                style={{color: filtered ? '##6366f1' : undefined}}
            />
        ),
        onFilter: (value, record) => {
            if (record[dataIndex]?.content) {
                return record[dataIndex].content
                    .toString()
                    .toLowerCase()
                    .includes(value.toLowerCase());
            } else {
                return record[dataIndex]
                    .toString()
                    .toLowerCase()
                    .includes(value.toLowerCase());
            }
        },
        // onFilterDropdownOpenChange: (visible) => {
        //     if (visible) {
        //         setTimeout(() => searchInput.current?.select(), 100);
        //     }
        // },
    });

    // handle rowspan
    const sortRowSpan = (data, col, groupBy) => {
        let values = [];
        if (groupBy.length < 1) {
            values = data.map((d) => {
                if (d[col].content) {
                    return d[col].content;
                } else {
                    return d[col];
                }
            });
        } else {
            groupBy.push(col);
            values = data.map((d) => {
                return groupBy
                    .map((col_) => {
                        if (d[col_].content) {
                            return d[col_].content;
                        } else {
                            return d[col_];
                        }
                    })
                    .join('');
            });
        }

        let colRowSpans = {};
        let i = 0;
        while (i < values.length) {
            let span = 1;
            let j = 1;
            while (i + j < values.length) {
                if (values[i + j] === values[i]) {
                    colRowSpans[i + j] = 0;
                    span += 1;
                    j += 1;
                } else {
                    break;
                }
            }
            colRowSpans[i] = span;
            i += j;
        }
        return colRowSpans;
    };

    if (columnAttrs && selectedColumns.length > 0) {
        cols = cols.map((d, i) => {
            if (columnAttrs[d.dataIndex]) {
                d = {...d, ...columnAttrs[d.dataIndex]};
            }
            if (!d.className) {
                d.className = d.dataIndex;
            }
            return d;
        });
        // handle column rowspan
        for (let i = 0; i < cols.length; i++) {
            const col = cols[i].dataIndex;
            if (columnAttrs[col]) {
                if (columnAttrs[col].rowMerge) {
                    let mergeBy = [];
                    if (columnAttrs[col].rowMergeBy) {
                        mergeBy = columnAttrs[col].rowMergeBy;
                    }
                    const colRowSpans = sortRowSpan(displayData, col, mergeBy);
                    cols[i].onCell = (_, index) => {
                        return {rowSpan: colRowSpans[index]};
                    };
                }

                // roated column header
                if (cols[i]['rotated']) {
                    let title = cols[i].key;
                    let class_name = 'rotated-th';
                    if (cols[i].title) {
                        title = cols[i].title;
                    }
                    if (cols[i].className) {
                        class_name = [
                            cols[i].className.toString(),
                            class_name,
                        ].join(' ');
                    }
                    cols[i]['className'] = class_name;
                    cols[i]['title'] = () => (
                        <span className="rotated-th-cell">{title}</span>
                    );
                }

                // link
                if (columnAttrs[col].renderType === 'link') {
                    cols[i]['render'] = (content) => {
                        if (content.href) {
                            return (
                                <a
                                    href={
                                        content.disabled
                                            ? undefined
                                            : content.href
                                    }
                                    target={
                                        content.target
                                            ? content.target
                                            : '_blank'
                                    }
                                    disabled={content.disabled}
                                >
                                    {content.content}
                                </a>
                            );
                        } else if (content.content) {
                            return content.content;
                        } else {
                            return ' ';
                        }
                    };
                    // Tag
                } else if (columnAttrs[col].renderType === 'tag') {
                    cols[i]['render'] = (content) => {
                        if (content) {
                            var tag_style = content.style;
                            if (content.fullwidth) {
                                tag_style = {
                                    ...tag_style,
                                    ...{
                                        width: '100%',
                                        height: '100%',
                                    },
                                };
                            }
                            return (
                                <Tag
                                    color={content.color}
                                    style={tag_style}
                                    bordered={
                                        content.bordered
                                            ? content.bordered
                                            : true
                                    }
                                >
                                    {content.href ? (
                                        <a href={content.href} target="_blank">
                                            {content.content}
                                        </a>
                                    ) : (
                                        content.content
                                    )}
                                </Tag>
                            );
                        } else {
                            return ' ';
                        }
                    };
                } else if (columnAttrs[col].renderType === 'tags') {
                    cols[i]['render'] = (content) => {
                        if (content.tags) {
                            return content.tags.map((tag) => {
                                var tag_style = tag.style;
                                if (tag.fullwidth) {
                                    tag_style = {
                                        ...tag_style,
                                        width: '100%',
                                        height: '100%',
                                    };
                                }
                                return (
                                    <Tag
                                        color={tag.color}
                                        style={tag_style}
                                        bordered={
                                            tag.bordered ? tag.bordered : true
                                        }
                                    >
                                        {tag.href ? (
                                            <a href={tag.href} target="_blank">
                                                {tag.content}
                                            </a>
                                        ) : (
                                            tag.content
                                        )}
                                    </Tag>
                                );
                            });
                        }
                    };
                } else if (columnAttrs[col].renderType === 'image') {
                    if (!className.includes('dvp-cell-img')) {
                        className = [className, 'dvp-cell-img'].join(' ');
                    }
                    cols[i]['render'] = (content) => {
                        if (content.src) {
                            return (
                                <img
                                    src={content.src}
                                    style={content.style}
                                />
                            );
                        } else {
                            return '';
                        }
                    };
                } else if (columnAttrs[col].renderType === 'styled-cell') {
                    if (!className.includes('dvp-styled-table')) {
                        className = [className, 'dvp-styled-table'].join(' ');
                    }
                    cols[i]['render'] = (content) => {
                        if (content) {
                            return (
                                <div
                                    style={content.style}
                                    className={content.className}
                                >
                                    {content.content}
                                </div>
                            );
                        } else {
                            return '';
                        }
                    };
                } else if (columnAttrs[col].renderType === 'checkbox') {
                    if (!className.includes('dvp-checkbox-table')) {
                        className = [className, 'dvp-checkbox-table'].join(' ');
                    }
                    cols[i]['render'] = (content) => {
                        if (content) {
                            let checked = false;
                            if (selectedValues) {
                                checked = selectedValues.includes(content.id);
                            }
                            let cellClassName = content.className
                                ? content.className
                                : '';
                            cellClassName = [
                                'dvp-checkbox',
                                cellClassName,
                            ].join(' ');
                            return (
                                <Checkbox
                                    value={content.id}
                                    defaultChecked={checked}
                                    className={cellClassName}
                                    disabled={
                                        content.disabled
                                            ? content.disabled
                                            : false
                                    }
                                    onChange={(e) => {
                                        if (e.target.checked) {
                                            checkedValues = [
                                                ...checkedValues,
                                                e.target.value,
                                            ];
                                        } else {
                                            checkedValues =
                                                checkedValues.filter(
                                                    (obj) =>
                                                        obj !== e.target.value
                                                );
                                        }
                                        setTimeout(function () {
                                            setProps({
                                                selectedValues: checkedValues,
                                            });
                                        }, 200);
                                    }}
                                >
                                    {content.content}
                                </Checkbox>
                            );
                        } else {
                            return '';
                        }
                    };
                } else if (columnAttrs[col].renderType === 'checkbox-icon') {
                    if (!className.includes('dvp-icon-table')) {
                        className = [className, 'dvp-icon-table'].join(' ');
                    }
                    cols[i]['render'] = (content) => {
                        if (content) {
                            let checked = false;
                            if (selectedValues) {
                                checked = selectedValues.includes(content.id);
                            }
                            let iconClassName = content.icon
                                ? content.icon.className
                                : '';
                            iconClassName = ['dvp-icon', iconClassName].join(
                                ' '
                            );
                            return (
                                <Checkbox
                                    value={content.id}
                                    defaultChecked={checked}
                                    disabled={
                                        content.disabled
                                            ? content.disabled
                                            : false
                                    }
                                    className={'dvp-icon-checkbox'}
                                    onChange={(e) => {
                                        if (e.target.checked) {
                                            checkedValues = [
                                                ...checkedValues,
                                                e.target.value,
                                            ];
                                        } else {
                                            checkedValues =
                                                checkedValues.filter(
                                                    (obj) =>
                                                        obj !== e.target.value
                                                );
                                        }
                                        setTimeout(function () {
                                            setProps({
                                                selectedValues: checkedValues,
                                            });
                                        }, 200);
                                    }}
                                >
                                    <DvpIcon
                                        icon={content.icon.icon}
                                        className={iconClassName}
                                        style={content.icon.style}
                                    />
                                </Checkbox>
                            );
                        } else {
                            return '';
                        }
                    };
                } else if (columnAttrs[col].renderType === 'ellipsis') {
                    cols[i].ellipsis = {showTitle: true};
                    cols[i]['render'] = (content) => {
                        let text = content;
                        if (text.length > ellipsisMaxLen) {
                            text = text.slice(0, ellipsisMaxLen - 3) + '...';
                            return (
                                <Tooltip placement="topLeft" title={content}>
                                    {text}
                                </Tooltip>
                            );
                        } else {
                            return content;
                        }
                    };
                }

                if (columnAttrs[col].filter) {
                    cols[i]['filterSearch'] = true;
                    if (!columnAttrs[col].filters) {
                        let uniqueValues = [];
                        if (isUndefined(columnAttrs[col].renderType)) {
                            uniqueValues = [
                                ...new Set(data.map((d) => d[col])),
                            ];
                        } else {
                            uniqueValues = [
                                ...new Set(data.map((d) => d[col].content)),
                            ];
                        }
                        uniqueValues.sort();
                        cols[i]['filters'] = uniqueValues.map((d) => ({
                            text: d,
                            value: d,
                        }));
                    } else {
                        cols[i]['filters'] = columnAttrs[col].filters;
                    }
                    cols[i]['onFilter'] = (value, record) => {
                        var matched = false;
                        if (record[col]?.content) {
                            matched = record[col].content == value;
                        } else {
                            matched = record[col] == value;
                        }
                        return matched;
                    };
                }

                if (columnAttrs[col].search) {
                    cols[i] = {
                        ...cols[i],
                        ...getColumnSearchProps(col),
                    };
                }
            }
        }

        if (cellUpdateOptimize) {
            cols = cols.map((item) => {
                return {
                    ...item,
                    shouldCellUpdate: (record, prevRecord) => {
                        if (isEqual(record, prevRecord)) {
                            return false;
                        }
                        return true;
                    },
                };
            });
        }
    }

    if (pagination) {
        if (pagination.position) {
            pagination = {
                ...pagination,
                position: [pagination.position],
            };
        }
        if (pagination.showTotal) {
            pagination = {
                ...pagination,
                showTotal: (total) => `Total ${total} items`,
            };
        }
    }

    return (
        <Table
            id={id}
            className={
                isString(className)
                    ? className
                    : className
                    ? useCss(className)
                    : undefined
            }
            style={style}
            bordered={bordered}
            dataSource={data}
            columns={cols}
            data={data}
            size={size}
            onChange={(pagi, filters, sorter, extra) => {
                let selectedData = data;
                pagi = filterObjNa(pagi);
                filters = filterObjNa(filters);

                // handle filters first
                if (Object.keys(filters).length > 0) {
                    Object.entries(filters).map(([col, fil]) => {
                        if (!isUndefined(fil)) {
                            selectedData = selectedData.filter((record) => {
                                // base on mode
                                if (columnAttrs[col].search === true) {
                                    let search_text = fil[0].toLowerCase();
                                    let v = '';
                                    if (record[col].content) {
                                        v = record[col].content
                                            .toString()
                                            .toLowerCase();
                                    } else {
                                        v = record[col]
                                            .toString()
                                            .toLowerCase();
                                    }
                                    return v.includes(search_text);
                                } else if (columnAttrs[col].filter == true) {
                                    if (record[col].content) {
                                        return fil.includes(
                                            record[col].content
                                        );
                                    } else {
                                        return fil.includes(record[col]);
                                    }
                                }
                            });
                        }
                    });
                }

                // handle pagination
                if (Object.keys(pagi).length > 0) {
                    const currenPage = pagi.current;
                    const pageSize = pagi.pageSize;
                    selectedData = selectedData.slice(
                        (currenPage - 1) * pageSize,
                        currenPage * pageSize
                    );
                }
                setDisplayData(selectedData);
            }}
            scroll={{
                x: maxWidth,
                y: maxHeight,
                scrollToFirstRowOnChange: true,
            }}
            pagination={pagination}
            data-dash-is-loading={
                (loading_state && loading_state.is_loading) || undefined
            }
        ></Table>
    );
};

DvpTable.defaultProps = defaultProps;
DvpTable.propTypes = propTypes;
export default DvpTable;
